body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "SF Pro Display", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--theme1000);
  color: var(--theme000);
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 10px;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.container {
  width: 100%;
  max-width: 1570px;
  padding: 0 12px;
  margin: 0 auto;
}

.wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 0 18px;
  }
}
@media screen and (min-width: 1024px) {
  .container {
    padding: 0 28px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    padding: 0 48px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    padding: 0 65px;
  }
}
.btn {
  min-height: 44px;
  padding: 10px 15px;
  font-weight: 800;
  font-size: 14px;
  line-height: 100%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  background: var(--accent);
  border-radius: 30px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.btn svg {
  width: 24px;
  height: 24px;
}
.btn-double span {
  margin: 0 10px;
}
.btn-outline {
  padding: 13px 15px;
  background: transparent;
  border: 2px solid var(--accent);
}

.btn-circle {
  width: 36px;
  min-width: 36px;
  height: 36px;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: var(--theme850);
}
.btn-circle svg {
  width: 26px;
  height: 26px;
}

.title {
  font-weight: 800;
  font-size: 16px;
  line-height: 120%;
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .btn-circle {
    width: 44px;
    min-width: 44px;
    height: 44px;
  }

  .title {
    font-size: 20px;
  }
}
@media screen and (min-width: 1280px) {
  .title {
    font-size: 24px;
  }
}
@media screen and (min-width: 1440px) {
  .title {
    font-size: 28px;
  }
}
.header {
  margin-bottom: 18px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--theme800);
}
.header__top {
  padding: 10px 0;
  background: var(--theme850);
}
.header__top-inner {
  display: flex;
  justify-content: space-between;
}
.header__lang {
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
  color: var(--theme200);
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
}
.header__lang-current {
  padding-right: 22px;
  position: relative;
}
.header__lang-current::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  height: 12px;
  background: url("../images/icons/arrow-down.svg") no-repeat center/contain;
}
.header__docs {
  display: none;
